import React from 'react';
import { useFormik } from 'formik';
import PropTypes from 'prop-types';

import Input from '../input/Input';
import PopUpLayout from '../popUpLayout/PopUpLayout';
import PrimaryButton from '../primaryButton/PrimaryButton';

import { ErrorMessage, Form, InputItem, InputList } from './commonPopUp.styled';
//          CommonPopUp
const CommonPopUp = ({
  id, // id    ,        , , id='register-button'
  avatar, //          ProfilePopUp,       
  title, //  , ,  title='Recovery password'
  children, //    ,    /  <GoogleAuth />    CommonPopUp
  onChange, //  ,     
  onClose, //   ,   
  onSubmit, //  
  hasIcon, //     hasICon={false}
  inputs, //  '    inputs={inputs},    .     
  initialValues, //    import { POP_UP_INITIAL_VALUES } from 'constants';        ', , const { AuthValues } = POP_UP_INITIAL_VALUES;
  validationSchema, //   helpers/validationSchemas
  buttonText, //  ,   ,   
  variant, //    PrimaryButton,      variant="formPopUp". Margin-top    -,    
  version, //    
  customInputProps, //  ,    -, 
}) => {
  const { handleChange, handleSubmit, values, errors, touched } = useFormik({
    initialValues,
    onSubmit: values => {
      onSubmit(values);
    },
    validationSchema,
  });

  return (
    <PopUpLayout title={title} handleClose={onClose}>
      <Form onSubmit={handleSubmit}>
        {avatar && children}
        <InputList>
          {inputs.map(inputProps => (
            <InputItem key={inputProps?.name}>
              <Input
                name={inputProps?.name}
                type={inputProps?.type}
                multiline={inputProps?.multiline}
                placeholder={inputProps?.placeholder}
                onChange={e => {
                  handleChange(e);
                  onChange(e);
                  if (customInputProps && customInputProps[inputProps?.name]) {
                    customInputProps[inputProps?.name](e);
                  }
                }}
                value={values[inputProps?.name]}
              />

              {errors[inputProps?.name] && touched[inputProps?.name] ? (
                <ErrorMessage>{errors[inputProps?.name]}</ErrorMessage>
              ) : null}
            </InputItem>
          ))}
        </InputList>
        {!avatar && children}
        <PrimaryButton
          id={id}
          variant={variant}
          hasIcon={hasIcon}
          version={version}
          type="submit"
        >
          {buttonText}
        </PrimaryButton>
      </Form>
    </PopUpLayout>
  );
};

CommonPopUp.propTypes = {
  id: PropTypes.string,
  title: PropTypes.string.isRequired,
  children: PropTypes.node,
  onClose: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  hasIcon: PropTypes.bool,
  inputs: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
      placeholder: PropTypes.string.isRequired,
      multiline: PropTypes.bool,
    })
  ).isRequired,
  initialValues: PropTypes.object.isRequired,
  validationSchema: PropTypes.object,
  buttonText: PropTypes.string.isRequired,
  version: PropTypes.string,
  variant: PropTypes.string,
};

export default CommonPopUp;
